export const getMostFollowerUsers = async (token: string) => {
  try {
    const { data } = await axios.get(`${baseURL}/users/most_followers`, {
      headers: {
        Authorization: 'Bearer ' + token,
      },
    });

    return data;
  } catch (error) {
    console.log(error);
  }
  

}

