//()
export const getCurrentUserInfo = async (token: string) => {
  try {
    const { status, data } = await axios.get(`${baseURL}/users`, {
      headers: {
        Authorization: 'Bearer ' + token,
      },
    });
    if (status === 200) {
      return data.currentUser;
    }
  } catch (error) {
    console.log(error);
  }
}

